import { isUndefined } from "lodash-es";
import { BaseService } from "../glue";
import { AuxiliaryEventKind, AuxiliaryModel } from "../models/auxiliaryBar";
import { arraylize, searchById } from "../utils";
class AuxiliaryBarService extends BaseService {
  state;
  constructor() {
    super("auxiliaryBar");
    this.state = new AuxiliaryModel();
  }
  get(id) {
    if (isUndefined(id))
      return;
    return this.getState().data.find(searchById(id));
  }
  getCurrent() {
    return this.getState().current;
  }
  getCurrentBar() {
    return this.get(this.getCurrent());
  }
  setCurrent(id) {
    this.dispatch((draft) => {
      draft.current = id;
    });
  }
  toggle(id) {
    const current = this.getCurrent();
    if (current === id) {
      this.setCurrent();
    } else {
      this.setCurrent(id);
    }
  }
  add(item) {
    this.dispatch((draft) => {
      draft.data.push(...arraylize(item));
    });
  }
  reset() {
    this.setState(new AuxiliaryModel());
  }
  // ===================== Subscriptions =====================
  onTabClick(callback) {
    this.subscribe(AuxiliaryEventKind.onTabClick, callback);
  }
}
export {
  AuxiliaryBarService
};
